.worlde {
     background-color: #252934;
     min-height: 100vh;
     display: flex;
     flex-direction: column;
     color: white;
     text-align: center;
     align-items: center;
     margin: 0 auto;
     font-size: calc(10px + 2vmin);
     scroll-behavior: smooth;
     scroll-snap-type: none;

     .home {   
          scroll-snap-align: start;
          margin: calc(3rem + 2vh);
          
          .area {
               justify-content: center;
               align-items: center;
               text-align: center;
               
               .label {
                    display: inline;
               }
     
               
               .textarea {
                    display: flex;
                    align-items: center;
                    display: inline;
                    resize: none;
                    width: 25vw;
                    height: calc(10px + 2vmin);
                    font-size: calc(5px + 2vmin);
                    justify-content: center;
                    text-align: center;
               }
          }
          
          .confirm {
               width: 30vw;
               height: 4vh;
               text-align: center;
               font-size: 3vh;
               font-weight: bold;
               margin-top: 3vh;
               margin-bottom: 3vh;
               overflow: hidden;
          }

          .words {
               width: 75%;
               min-height: 1rem;
               background-color: transparent;
               position: relative;
               overflow: scroll;
               scrollbar-width: none; // firefox
               &::-webkit-scrollbar {
                    display: none;
               } // Chrome, Safari
          }
     }
}